body {
	margin: 0; //Remove Margin
	@include body-text( body );

	background: $main-background; //Primary Background Color
	word-wrap: break-word; //Break word when overflow
}

main { //Main Element
    background-color: $main-background; //Main Background Color
    padding: 8px 12px 8px 12px; //Padding for the Main
	min-height: 450px; //Min Body height in case of empty content
}

img { //Image Element
	height: auto; //Auto Resize Image height
	display: block;
	width: 100%; //Full Div Width
}

button { //Button Element
	margin: 4px 4px 4px 4px;
    border-radius: 8px;
	border: none;
	display: inline-flex;
	text-align: center;
	align-items: center;
	justify-content: center;

	a{
		color: white;
		text-decoration: none !important;
		&:hover,&:active {
			color: white;
		}
	}
}

//Table

table{
	border-collapse: separate;
}

table td, table td *, table th, table th * {
    vertical-align: top;
}


//Topography

h1 {
	@include body-text( h1 )
}

h2 {
	@include body-text( h2 )
}

h3 {
	@include body-text( h3 )
}

h4 {
	@include body-text( h4 )
}

h5 {
	@include body-text( h5 )
}

h6 {
	@include body-text( h6 )
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0px 0px 12px 0px;
	text-rendering: optimizelegibility;
}